.gradient {
  width: 100%;
  height: calc(100vh - 48px);
  margin-top: 48px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.title {
  color: #fff;
  height: 120px;
  font-size: 80px;
  font-weight: bold;
  font-family: "Nanum Gothic", sans-serif;
}

.logo-invert {
  width: 250px;
}

.body {
  color: #fff;
  font-size: 16px;
  font-family: "Nanum Gothic", sans-serif;
}

.section-body {
  width: 100%;
  height: 80vh;
  background-color: #f1f5f7;
  display: flex;
}

.word {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  font-family: "Nanum Gothic", sans-serif;
  font-size: 48px;
}

.pic {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.content-image {
  height: 250px;
}
